@each $s, $sv in $spacings {

    @each $t, $tv in $spacing-types {
        .u-#{$tv}-#{$s} {
            #{$t}: $sv;
        }

        .u-#{$tv}t-#{$s} {
            #{$t}-top: $sv;
        }

        .u-#{$tv}b-#{$s} {
            #{$t}-bottom: $sv;
        }

        .u-#{$tv}l-#{$s} {
            #{$t}-left: $sv;
        }

        .u-#{$tv}r-#{$s} {
            #{$t}-right: $sv;
        }

        .u-#{$tv}x-#{$s} {
            #{$t}-left: $sv;
            #{$t}-right: $sv;
        }

        .u-#{$tv}y-#{$s} {
            #{$t}-top: $sv;
            #{$t}-bottom: $sv;
        }
    }
}

@each $bp, $bpv in $breakpoints {

    @each $s, $sv in $spacings {
        @media (min-width: $bpv) {

            @each $t,
            $tv in $spacing-types {
                .u-#{$tv}-#{$s}-#{$bp} {
                    #{$t}: $sv;
                }

                .u-#{$tv}t-#{$s}-#{$bp} {
                    #{$t}-top: $sv;
                }

                .u-#{$tv}b-#{$s}-#{$bp} {
                    #{$t}-bottom: $sv;
                }

                .u-#{$tv}l-#{$s}-#{$bp} {
                    #{$t}-left: $sv;
                }

                .u-#{$tv}r-#{$s}-#{$bp} {
                    #{$t}-right: $sv;
                }

                .u-#{$tv}x-#{$s}-#{$bp} {
                    #{$t}-left: $sv;
                    #{$t}-right: $sv;
                }

                .u-#{$tv}y-#{$s}-#{$bp} {
                    #{$t}-top: $sv;
                    #{$t}-bottom: $sv;
                }
            }
        }
    }
}