// direction
.u-flex-direction-column {
	flex-direction: column;
}

.u-flex-direction-column-reverse {
	flex-direction: column-reverse;
}

.u-flex-direction-row {
	flex-direction: row;
}

.u-flex-direction-row-reverse {
	flex-direction: row-reverse;
}

// Justify
.u-justify-content-center {
	justify-content: center;
}

.u-justify-content-start {
	justify-content: start;
}

.u-justify-content-end {
	justify-content: end;
}

.u-justify-content-flex-start {
	justify-content: flex-start;
}

.u-justify-content-flex-end {
	justify-content: flex-end;
}

.u-justify-content-left {
	justify-content: left;
}

.u-justify-content-right {
	justify-content: right;
}

.u-justify-content-space-between {
	justify-content: space-between;
}

.u-justify-content-space-around {
	justify-content: space-around;
}

.u-justify-content-space-evenly {
	justify-content: space-evenly;
}

.u-justify-content-stretch {
	justify-content: stretch;
}

// Align items
.u-align-items-center {
	align-items: center;
}

.u-align-items-flex-start {
	align-items: flex-start;
}

.u-align-items-flex-end {
	align-items: flex-end;
}

.u-align-items-start {
	align-items: start;
}

.u-align-items-end {
	align-items: end;
}

.u-align-items-baseline {
	align-items: baseline;
}

.u-align-items-self-start {
	align-items: self-start;
}

.u-align-items-self-end {
	align-items: self-end;
}

// Align Self
.u-align-self-center {
	align-self: center;
}

.u-align-self-flex-start {
	align-self: flex-start;
}

.u-align-self-flex-end {
	align-self: flex-end;
}

.u-align-self-start {
	align-self: start;
}

.u-align-self-end {
	align-self: end;
}

.u-align-self-self-start {
	align-self: self-start;
}

.u-align-self-self-end {
	align-self: self-end;
}

.u-flex-grow {
	flex-grow: 1;

	& [class*="o-cell"] {
		flex-grow: 1;
	}
}

.u-flex-shrink {
	flex-shrink: 1;

	& [class*="o-cell"] {
		flex-shrink: 1;
	}
}

.u-flex-no-grow {
	flex-grow: 0;

	& [class*="o-cell"] {
		flex-grow: 0;
	}
}

.u-flex-no-shrink {
	flex-shrink: 0;

	& [class*="o-cell"] {
		flex-shrink: 0;
	}
}

// wrap
.u-flex-nowrap {
	flex-wrap: nowrap!important;
}

// Order
@for $i from 0 through 10 {
	.u-order-#{$i} {
		order: $i;
	}
}

@each $k, $v in $breakpoints {

	@media (min-width: $v) {

		// direction
		.u-flex-direction-column-#{$k} {
			flex-direction: column;
		}

		.u-flex-direction-column-reverse-#{$k} {
			flex-direction: column-reverse;
		}

		.u-flex-direction-row-#{$k} {
			flex-direction: row;
		}

		.u-flex-direction-row-reverse-#{$k} {
			flex-direction: row-reverse;
		}

		// Justify
		.u-justify-content-center-#{$k} {
			justify-content: center;
		}

		.u-justify-content-start-#{$k} {
			justify-content: start;
		}

		.u-justify-content-end-#{$k} {
			justify-content: end;
		}

		.u-justify-content-flex-start-#{$k} {
			justify-content: flex-start;
		}

		.u-justify-content-flex-end-#{$k} {
			justify-content: flex-end;
		}

		.u-justify-content-left-#{$k} {
			justify-content: left;
		}

		.u-justify-content-right-#{$k} {
			justify-content: right;
		}

		.u-justify-content-space-between-#{$k} {
			justify-content: space-between;
		}

		.u-justify-content-space-around-#{$k} {
			justify-content: space-around;
		}

		.u-justify-content-space-evenly-#{$k} {
			justify-content: space-evenly;
		}

		.u-justify-content-stretch-#{$k} {
			justify-content: stretch;
		}

		// Align items
		.u-align-items-center-#{$k} {
			align-items: center;
		}

		.u-align-items-flex-start-#{$k} {
			align-items: flex-start;
		}

		.u-align-items-flex-end-#{$k} {
			align-items: flex-end;
		}

		.u-align-items-start-#{$k} {
			align-items: start;
		}

		.u-align-items-end-#{$k} {
			align-items: end;
		}

		.u-align-items-baseline-#{$k} {
			align-items: baseline;
		}

		.u-align-items-self-start-#{$k} {
			align-items: self-start;
		}

		.u-align-items-self-end-#{$k} {
			align-items: self-end;
		}

		// Align Self
		.u-align-self-center-#{$k} {
			align-self: center;
		}

		.u-align-self-flex-start-#{$k} {
			align-self: flex-start;
		}

		.u-align-self-flex-end-#{$k} {
			align-self: flex-end;
		}

		.u-align-self-start-#{$k} {
			align-self: start;
		}

		.u-align-self-end-#{$k} {
			align-self: end;
		}

		.u-align-self-self-start-#{$k} {
			align-self: self-start;
		}

		.u-align-self-self-end-#{$k} {
			align-self: self-end;
		}

		.u-flex-grow-#{$k} {
			flex-grow: 1;
		}

		.u-flex-shrink-#{$k} {
			flex-shrink: 1;
		}

		.u-flex-no-grow-#{$k} {
			flex-grow: 0;
		}

		.u-flex-no-shrink-#{$k} {
			flex-shrink: 0;
		}

		@for $i from 0 through 10 {
			.u-order-#{$i}-#{$k} {
				order: $i;
			}
		}

	}

}