.u-t {
    &-300 {
        font-weight: normal;
    }

    &-bold {
        font-weight: bold;
    }

    &-center {
        text-align: center;
    }

    &-left {
        text-align: left;
    }

    &-right {
        text-align: right;
    }

    &-uppercase {
        text-transform: uppercase;
    }

    &-lowercase {
        text-transform: lowercase;
    }

    &-underlined {
        text-decoration: underline;
    }

    &-pointer {
        cursor: pointer;
    }

    @each $key, $value in $font-sizes {
        &-#{$key} {
            font-size: $value;
        }
    }
}

@each $k, $v in $breakpoints {
    @media (min-width: $v) {
        .u-t {
            &-300-#{$k} {
                font-weight: normal;
            }

            &-bold-#{$k} {
                font-weight: bold;
            }

            &-center-#{$k} {
                text-align: center;
            }

            &-left-#{$k} {
                text-align: left;
            }

            &-right-#{$k} {
                text-align: right;
            }

            &-uppercase-#{$k} {
                text-transform: uppercase;
            }

            &-lowercase-#{$k} {
                text-transform: lowercase;
            }

            @each $key, $value in $font-sizes {
                &-#{$key}-#{$k} {
                    font-size: $value;
                }
            }
        }

    }
}