html {
    font-family: $font-heading;
    font-size: 12px;
    $min_width: 320px;
    $max_width: $bp-4k;
    $min_font: 12px;
    $max_font: 22px;

    @include fluid-type($min_width, $max_width, $min_font, $max_font);

}

p, a{
    font-size: 1rem;
}

p {
    line-height: 1.5;
    margin-bottom: $space-2;
}

a {
    transition: color $duration $ease-in-out-quad;
    text-decoration: none;
    cursor: pointer;
    color: $color-black;
}

address {
    font-style: 300;
}

strong, b {
    font-weight: bold;
}

sup {
    font-size: 0.5rem;
}