.title {
    text-align: center;
    font-family: $font-heading;
    font-size: $font-size-h3;
    text-transform: uppercase;
    // line-height: 0.8;

    @media (min-width: $bp-large-up) {
        font-size: $font-size-h2;
    }

    @media (min-width: $bp-xlarge-up) {
        font-size: $font-size-h1;
    }

    &-container {
        text-align: center;

        margin-bottom: $space-4;
    }

    &__we-are {
        display: block;
        color: $color-yellow;
        margin-bottom: $space-1 * 0.5;
        overflow: hidden;
        &__inner {
            display: block;
        }
    }
    &__text {
        display: block;
        color: $color-white;
        margin-bottom: $space-1 * 0.5;
        overflow: hidden;

        &__inner {
            display: block;
            position: relative;
            padding-right: $space-1 * 1.5;

            @media (min-width: $bp-large-up) {
                padding-right: $space-1 * 1.5;
            }

            @media (min-width: $bp-xlarge-up) {
                padding-right: $space-2;
            }
        }

        &__period {
            position: absolute;
            right: 0;
            bottom: $space-1 * 0.9;
            width: $space-1;
            height: $space-1;
            fill: $color-yellow;

            @media (min-width: $bp-large-up) {
                width: $space-1;
                height: $space-1;
            }

            @media (min-width: $bp-xlarge-up) {
                width: $space-1 * 1.5;
                height: $space-1 * 1.5;
            }
        }
    }

    &--inline {
        .title {
            &__text,
            &__we-are {
                display: inline-block;
            }
        }
    }
}