input {
    border-radius: 0;

    &:focus,
    &:active {
        outline: none;
    }

    &[type="text"],
    &[type="submit"],
    &[type="email"] {
        -webkit-appearance: none;
    }

    &[type="submit"] {
        padding: $space-1 $space-2;
        transition: background-color 0.25s $ease-in-out-quad;
        cursor: pointer;

        &:hover {}
    }

    &[type="text"],
    &[type="email"] {
        padding: $space-1;
    }

    &[type="email"] {
        min-width: 145px;

        @media (min-width: $bp-small-up) {
            min-width: 200px;
        }
    }
}