html, body {
	margin: 0;
	padding: 0;
}

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-tap-highlight-color: transparent;
	font-family: $font-body;
	font-weight: 600;
	hyphens: none;
	background-color: $color-black;
	color: $color-white;
	overflow: hidden;
}

* {
	box-sizing: border-box;
	&::selection {
		background: $color-black;
	}
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: bottom;
	margin: 0 auto;
}

video {
	max-width: 100%;
	height: auto;
}

figure, h1, h2, h3, h4, h5, p, ul, ol, li, blockquote, button {
	margin: 0;
	padding: 0;
}

ul, ol, li {
	list-style: none;
}

address {
	font-style: normal;
}

button {
	border: none;
	width: auto;
	overflow: visible;
	background-color: transparent;
	color: inherit;
	font: inherit;
	appearance: none;
	outline: 0;
	cursor: pointer;
}