.list {
    &--simple {
        padding: 0;
        margin: 0;
        list-style: none;
    }

    &__item {
        margin-bottom: 0.1rem;
    }

    &--horizontal &__item {
        display: inline-block;
        font-size: 0.9rem;
        margin-right: $space;
        vertical-align: middle;

        &:last-of-type {
            margin-right: 0;
        }
    }
}