.main {
    text-align: center;
    margin-bottom: 5rem;

    @media (min-width: $bp-small-up) {
        margin-bottom: 0;
    }

    &__title {
        font-size: $font-size-h2;
        font-family: $font-body;
        font-weight: 600;
        font-style: normal;

        @media (min-width: $bp-small-up) {
            font-size: $font-size-h1;
        }
    }

    &__date {
        font-weight: 700;
    }
}