.artists {
    display: flex;
    flex-direction: column;

    .title__text {
        padding: 0;
    }

    &__title {
        position: relative;
        display: block;
        transform-origin: top;
        padding-right: $space-1 * 1.5;

            @media (min-width: $bp-large-up) {
                padding-right: $space-1 * 1.5;
            }

            @media (min-width: $bp-xlarge-up) {
                padding-right: $space-2;
            }
    }

    &__selected-artist {
        &__inner {
            display: block;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%) rotateX(-90deg);
            transform-origin: bottom;
            white-space: nowrap;
            padding-right: $space-1 * 1.5;

            @media (min-width: $bp-large-up) {
                padding-right: $space-1 * 1.5;
            }

            @media (min-width: $bp-xlarge-up) {
                padding-right: $space-2;
            }
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        color: $color-white;
    }

    &__letter {
        flex: 0 1 auto;
        position: relative;
        margin-bottom: $space-2;
        margin-right: $space-2;

        &__text {
            position: absolute;
        }

        &__list {
            padding-left: $space-2;

            &__item {
                text-transform: uppercase;
                transition: color $duration-sm $ease-in-out-quad;

                &.is-active {
                    color: $color-yellow;
                    // transition: color $duration-sm $ease-out-quad;
                }
            }
        }
    }
}