.news-listing {
    display: inline-block;
    width: 33%;

    &-container {
        display: flex;
        flex-direction: row;
    }

    &__image {
        margin: 0;
        padding: 0;
        transition: transform $duration-lg $ease-in-out-quad, filter $duration-lg $ease-in-out-quad;
    }

    &__image-container {
        display: block;
        position: relative;
        overflow: hidden;
        &:hover {
            .news-listing {
                &__image {
                    filter: sepia(100%) saturate(300%) brightness(70%);
                    transform: scale(1.2, 1.2);
                }
                &__date,
                &__excerpt,
                &__read-more {
                    opacity: 1;
                    transform: translateY(0%);
                    transition: transform $duration-lg $ease-in-out-quart ($duration-sm * 0.5), opacity $duration-lg $ease-in-out-quart ($duration-sm * 0.5);
                }
                &__excerpt {
                    transition: transform $duration-lg $ease-in-out-quart $duration-sm, opacity $duration-lg $ease-in-out-quart $duration-sm;
                }
                &__read-more {
                    transition: transform $duration-lg $ease-in-out-quart ($duration-sm * 1.5), opacity $duration-lg $ease-in-out-quart ($duration-sm * 1.5);
                }
            }
        }
    }

    &__details {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 20%;
        color: $color-white;
        z-index: 1;
    }

    &__date {
        opacity: 0;
        transform: translateY($space-2);
        transition: transform $duration-md $ease-in-out-quart, opacity $duration-md $ease-in-out-quart;
        margin-bottom: $space-2;
        &__month,
        &__day,
        &__year {
            display: inline-block;
            font-size: $font-size-sm;
        }

        &__month {
            position: relative;
            padding-right: $space-1 * 1.2;

            &:after {
                content: '';
                position: absolute;
                top: 50%;
                right: 0;
                width: $space-1;
                height: 2px;
                background-color: $color-white;
            }
        }
    }

    &__excerpt {
        font-family: $font-heading;
        font-size: $font-size-md;
        text-transform: uppercase;
        margin-bottom: $space-3;
        opacity: 0;
        transform: translateY($space-2);
        transition: transform $duration-md $ease-in-out-quart, opacity $duration-md $ease-in-out-quart;

        @media (min-width: $bp-xlarge-up) {
            font-size: $font-size-h6;
        }
    }

    &__read-more {
        text-decoration: underline;
        opacity: 0;
        transform: translateY($space-2);
        transition: transform $duration-md $ease-in-out-quart, opacity $duration-md $ease-in-out-quart;
    }
}