.h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: $font-heading;
    font-style: normal;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: $space-1;
    display: inline-block;
    line-height: 1;
}

.h1 {
    font-size: $font-size-h1;
}

.h2 {
    font-size: $font-size-h2;
}

.h3 {
    font-size: $font-size-h3;
}

.h4 {
    font-size: $font-size-h4;
}

.h5 {
    font-size: $font-size-h5;
}

.h6 {
    font-size: $font-size-h6;
}