.container {
	padding: 0 ($space-1 / 2);
	width: 100%;

	@media (min-width: $bp-medium-up) {
		padding: 0 10vw;
	}

	@media (min-width: $bp-4k) {
		max-width: $bp-4k;
	}

	&--fluid {
		max-width: 100%;
		padding: 0;

		@media (min-width: $bp-medium-up) {
			padding: 0;
		}
	}
}

.grid {
	max-width: 100%;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	margin-left: -$space-3;
}

[class*="cell"] {
	padding-left: $space-3;
}

// Cells and offsets
.cell-auto {
	width: auto;
}

@for $i from $grid-columns through 1 {
	.cell-#{$i} {
		width: #{(100% / ($grid-columns / $i))};
		padding-left: $space-3;
	}
	.offset-#{$i} {
		margin-left: calc((100% / (#{$grid-columns} / #{$i})));
	}
}

@each $k, $v in $breakpoints {
	.container--fluid-#{k} {
		max-width: 100%;
	}
	@for $i from $grid-columns through 1 {
		@media (min-width: $v) {
			.cell-#{$i}-#{$k} {
				width: #{(100% / ($grid-columns / $i))};
				padding-left: $space-3;
			}

			.cell-aut#{$k} {
				width: auto;
			}

			.offset-#{$i}-#{$k} {
				margin-left: calc((100% / (#{$grid-columns} / #{$i})));
			}

			.offset-0-#{$k} {
				padding-left: 0;
			}
		}
	}
}