@font-face {
    font-family: 'Sharp Sans';
    src: url('../../fonts/SharpSans-Extrabold.eot');
    src: url('../../fonts/SharpSans-Extrabold.woff2') format('woff2'),
        url('../../fonts/SharpSans-Extrabold.woff') format('woff');
}


@font-face {
    font-family: 'Founders Grotesk';
    src: url('../../fonts/FoundersGroteskWeb-Bold.eot');
    src: url('../../fonts/FoundersGroteskWeb-Bold.woff2') format('woff2'),
        url('../../fonts/FoundersGroteskWeb-Bold.woff') format('woff');
}

@font-face {
    font-family: 'Founders Grotesk';
    src: url('../../fonts/FoundersGroteskWeb-Regular.eot');
    src: url('../../fonts/FoundersGroteskWeb-Regular.woff2') format('woff2'),
        url('../../fonts/FoundersGroteskWeb-Regular.woff') format('woff');
}


$font-heading: 'Sharp Sans', sans-serif;
$font-body: 'Founders Grotesk', sans-serif;

// Heading sizes
$font-size-h1: 6.375rem;
$font-size-h2: 3rem;
$font-size-h3: 2.625rem;
$font-size-h4: 2.25rem;
$font-size-h5: 1.75rem;
$font-size-h6: 1rem;

// General fotn sizes
$font-size-xsm: 0.666rem;
$font-size-sm: 0.875rem;
$font-size-md: 1;
$font-size-xlg: 3.375rem;


$font-sizes:(
h1: $font-size-h1,
h2: $font-size-h2,
h3: $font-size-h3,
h4: $font-size-h4,
h5: $font-size-h5,
h6: $font-size-h6,
xsm: $font-size-xsm,
sm: $font-size-sm,
md: $font-size-md,
xlg: $font-size-xlg,
);
