.u-d-flex {
    display: flex;
    flex-wrap: wrap;
}

.u-d-inline-flex {
    display: inline-flex;
}

.u-d-block {
    display: block;
}

.u-d-inline-block {
    display: inline-block;
}

.u-d-none {
    display: none;
}

.u-d-list-item {
    display: list-item;
}

.u-d-table-row {
    display: table-row;
}

.u-overflow-hidden {
    overflow: hidden;
}

@each $k, $v in $breakpoints {

    @media (min-width: $v) {

        .u-d-flex-#{$k} {
            display: flex;
        }

        .u-d-inline-flex-#{$k} {
            display: inline-flex;
        }

        .u-d-block-#{$k} {
            display: block;
        }

        .u-d-inline-block-#{$k} {
            display: inline-block;
        }

        .u-d-none-#{$k} {
            display: none;
        }

        .u-d-list-item-#{$k} {
            display: list-item;
        }

        .u-overflow-hidden-#{$k} {
            overflow: hidden;
        }

        .u-d-table-row {
            display: table-row;
        }

    }

}