.directory {
    @include linear-gradient($color-orange, $color-pink);

    &__nav {
        &__list {
            text-align: center;
            &__item {
                &__link {
                    font-family: $font-heading;
                    font-size: 5rem;
                    text-transform: uppercase;
                    line-height: 1;

                    @media (min-width: $bp-large-up) {
                        font-size: 6rem;
                    }

                    @media (min-width: $bp-xlarge-up) {
                        font-size: 8rem;
                    }

                    @media (min-width: $bp-xxlarge-up) {
                        font-size: 10rem;
                    }
                }
            }
        }
    }
}